<template>
    <div class="flex-page">
        <van-nav-bar class="flex-full" ref="flexFull">
            <div slot="left" class="leftBar whiteNavBar">
                <div class="backIcon" @click="goBack()">
                    <img :src="icon.backBlack" alt="">
                </div>
                <div class="searhInputBox">
                    <div class="searchInput" @click="goToGoodsSearch()">{{searchKeyword.length > 0 ? searchKeyword : defaultWord}}</div>
                </div>
                <div class="searchBtn">
                    <img class="collectBtn" :src="isCollect?icon.collectionBlackSel:icon.collectionBlackNor" @click="collectBtn()" alt="">
                    <div class="shopIcon" @click="goToShoppingCart()">
                        <img :src="icon.shopCartBlack" alt="">
                        <div class="goodsNum" :class="skuTypeNum>=100?'goodsNumMax':''" v-show="skuTypeNum!=0">
                            {{skuTypeNum<=99?skuTypeNum:'99+'}}
                        </div>
                    </div>
                </div>
            </div>
        </van-nav-bar>
        <div class="post-wrapper">
            <div class="shopInfoBox">
                <div class="shopTitle">商家资料</div>
                <div class="shopImgBox">
                    <div class="shopImg">
                        <img :src="shopInfo.shopPicUrl" alt="">
                    </div>
                    <div class="shopInfo">
                        <div class="shopName">{{shopInfo.shopName}}</div>
                        <div class="shopScore">
                            <div class="shopStars" v-if="shopInfo.shopLevel != 0">
                                <img v-for="shopLevelItem in Math.floor(shopInfo.shopLevel)" :key="'shopLevelItem'+shopLevelItem"
                                     src="@/assets/images/common/icon_evaluate_red.png" alt="">
                                <img v-if="shopInfo.shopLevel%1!=0" src="@/assets/images/common/icon_evaluate_gradient.png" alt="">
                                <img v-for="noShopLevelItem in Math.floor(5-shopInfo.shopLevel)"
                                     :key="'noShopLevelItem'+noShopLevelItem"
                                     src="@/assets/images/common/icon_evaluate_gray.png" alt="">
                            </div>
                            <div class="shopStarNum" v-if="shopInfo.shopLevel != 0">{{shopInfo.shopLevel}}</div>
                            <div class="noShopStarNum" v-if="shopInfo.shopLevel == 0">未评价</div>
                            <div class="saleNum">月售{{shopInfo.orderCount | threshold}}</div>
                        </div>
                    </div>
                </div>
                <div class="shopTitle">店内公告</div>
                <div class="noticeTxt">{{noticeTxt}}</div>
                <div class="shopTitle">经营分类</div>
                <div class="manageClassifyList">
                    <div class="manageClassifyItem" v-for="(cateNameItem,cateNameIndex) in shopInfoList.cateNameList"
                        :key="'cateNameItem'+cateNameIndex">{{cateNameItem}}</div>
                </div>
                <div class="addressBox">
                    <div class="addressInfo">
                        <div class="shopTitle">商家地址</div>
                        <div class="shopAddress">{{shopInfoList.shopAddr}}</div>
                    </div>
                    <div class="phoneIcon"  @click="call">
                        <img src="@/assets/images/mall/icon_contact_cut.png" alt="">
                    </div>
                </div>

                <div class="shopTitle">商家简介</div>
                <div class="synopsisTxt">{{shopInfoList.introduceInfo }}</div>
            </div>
        </div>
        <!--打电话弹窗-->
        <van-popup v-model="isShowPhoneSelect" position="bottom" class="vanPopup" @close="onCancel">
            <div class="phoneSelectBox">
                <div class="phoneSelect" @click="callPhone(shopInfoList.servicePhone)">
                    <div>商家电话：</div>
                    <div class="phoneNum">{{shopInfoList.servicePhone}}</div>
                </div>
            </div>
            <div class="cancelPhoneBtn" @click="onCancel">取消</div>
        </van-popup>
        <serviceItem :serviceInfo="serviceInfo" @setServiceInfo="setServiceInfo"></serviceItem>

    </div>
</template>

<script>
    import {
        getOneShopTitleService,getShopInfoForAppService,
        getSearchKeywordService,getSkuTypeNumService,
        getCollectListService,deleteCollectService,saveCollectService
    } from "../../services/goodsService";
    import serviceItem from "../superiorProduct/serviceItem"
    export default {
        name: "shopInfoPage",
        data(){
            return{
                isShowPhoneSelect:false,
                shopCode:"",    //店铺code
                shopInfo:{
                    shopLevel: 0
                },    //店铺信息
                shopInfoList:{},
                noticeTxt:"",   //店铺公告
                defaultWord: '',
                searchKeyword: '',
                isCollect: false, //店铺是否收藏
                collectCode: "", //店铺收藏码
                skuTypeNum: 0,//购物车种类数量
                icon: {
                    backBlack: require("@/assets/images/common/nav_return_black.png"), //透明状态下 返回
                    collectionBlackNor: require("@/assets/images/newSlicing/mall_icon_collection_nor_black.png"), //不透明状态下 未收藏
                    collectionBlackSel: require("@/assets/images/newSlicing/mall_icon_collection_sel_red.png"), //不透明状态下 收藏
                    shopCartBlack: require("@/assets/images/newSlicing/icon_shop_cut.png"),//不透明状态下 购物车
                },
                serviceInfo : null,
                showServiceItem :false,
            }
        },
        components:{
            serviceItem
           
        },
        filters: {
            threshold(data) {
                return data >= 10000 ?  (data/10000>=10?'9万+':Math.floor(data / 10000) + '万+') : data
            },
        },
        mounted(){

        },
        activated(){
            if(this.$route.params.shopCode){
                this.shopCode = this.$route.params.shopCode;
                this.getOneShopTitle();//获取店铺信息
                this.getShopInfoForApp();
            }
            this.getSearchKeyword();
            this.getSkuTypeNum();
            this.getCollectList(); //查询店铺是否被收藏
        },
        methods: {
            getShopInfoForApp(){
                let paramData = {
                    shopCode: this.shopCode,
                }
                getShopInfoForAppService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.shopInfoList = res.data;
                        this.shopInfoList.shopAddr = res.data.shopAddr==null?"商家未设置店铺地址":res.data.shopAddr;
                        this.shopInfoList.introduceInfo = res.data.introduceInfo==null?"商家未设置店铺简介":res.data.introduceInfo;
                    }
                }).catch(error => {
                });
            },
            /**
             * 获取店铺信息
             * @param
             * shopCode  Number      店铺CODE
             **/
            getOneShopTitle() {
                let paramData = {
                    shopCode: this.shopCode,
                    areaCode: this.$userInfo.AREA_CODE
                }
                getOneShopTitleService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.shopInfo = res.data;
                        this.noticeTxt = res.data.noticeInfo==null?"商家未设置店铺公告":res.data.noticeInfo;
                    }
                }).catch(error => {
                });
            },
            // 联系商家
            callPhone(phone) {
                this.onCancel();
                if (this.$deciceType=='phone'){
                    window.location.href = "tel:" + phone;
                }
            },
            //取消电话弹窗
            onCancel(){
                this.isShowPhoneSelect = false;
            },
            /**
             * 查询店铺是否被收藏
             * @params
             * collectType String  店铺收藏标识
             * contentCode String  店铺CODE
             * userCode    Number
             */
            getCollectList() {
                let paramData = {
                    collectType: "02",
                    contentCode: this.shopCode,
                    userCode: this.$userInfo.OWNER_CODE
                }
                getCollectListService(paramData).then(res => {
                    if (res.resultCode == "0") {
                        if (res.data.size > 0) {
                            this.collectCode = res.data.list[0].collectCode;//收藏码
                            this.isCollect = true; //收藏
                        } else {
                            this.isCollect = false; //不收藏
                        }

                    }
                }).catch(error => {
                })
            },
            //更改收藏状态
            collectBtn() {
                if (this.isCollect) {
                    //取消收藏
                    this.isCollect = false; //收藏
                    this.deleteCollect();
                } else {
                    //收藏
                    this.isCollect = true; //收藏
                    this.saveCollect();
                }
            },
            /**
             * 收藏该店铺
             * @param
             * collectType  String  收藏店铺的标识
             * contentCode  Number  店铺CODE
             * userCode     Number  业主CODE
             */
            saveCollect(isFirst = true) {
                let paramData = {
                    collectType: "02",
                    contentCode: this.shopCode,
                    userCode: this.$userInfo.OWNER_CODE
                }
                saveCollectService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.collectCode = res.data.collectCode;
                        this.isCollect = true;
                    } else {
                        if (isFirst) {
                            this.saveCollect(false)
                        } else {
                            this.isCollect = false; //不收藏
                        }
                    }
                }).catch(error => {
                });
            },
            /**
             * 取消该店铺的收藏
             * @param
             * collectCodes  Arr  店铺的收藏码
             */
            deleteCollect(isFirst = true) {
                let paramData = {
                    collectCodes: [this.collectCode]
                }
                deleteCollectService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.isCollect = false;
                    } else {
                        if (isFirst) {
                            this.deleteCollect(false);
                        } else {
                            this.isCollect = true; //收藏
                        }
                    }
                }).catch(error => {
                });
            },
            /**
             * 查询购物车数量
             * ownerCode  Number  业主CODE
             */
            getSkuTypeNum() {
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE
                }
                getSkuTypeNumService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.skuTypeNum = res.data.skuTypeNum;
                    }
                }).catch(error => {
                })
            },
               /**
             * 拨打电话
             */
            call() {
           
                this.serviceInfo = this.shopInfoList.serviceInfo;
                this.showServiceItem = true;
            },
            setServiceInfo(obj){
                this.serviceInfo = obj;
            },
            // 获取搜索关键词
            getSearchKeyword() {
                let reqData = {
                    areaCode: this.$userInfo.AREA_CODE,
                    fromType: 1
                }
                getSearchKeywordService(reqData).then(res=>{
                    if (res.resultCode == 0) {
                        this.searchKeyword = '';
                        this.defaultWord = '搜索好物';
                    }
                }).catch(error=>{
                    this.defaultWord = '搜索好物';
                    console.log(error);
                })
            },
            //返回上一页
            goBack() {
                this.$router.go(-1);
            },
            //去搜索页面
            goToGoodsSearch(speechTxt) {
                this.$router.push({
                    name: 'shopGoodsSearch',
                    params: {
                        shopCode: this.shopCode,
                        speechTxt: speechTxt,
                        isRefresh: 1,
                        placeholderWord: this.searchKeyword
                    }
                })
            },
            //去购物车页面
            goToShoppingCart() {
                this.$router.push({
                    name: 'shoppingCart',
                    params: {}
                })
            },
        }
    }
</script>

<style scoped lang="scss">
    .flex-page {
        width: 100%;
        height: 100%;
        .post-wrapper {
            height: 100%;
            overflow: scroll;
        }
        .postWrapper{
            overflow: hidden;
        }
    }
    /deep/.van-nav-bar{
        box-shadow:none;
    }
    .van-nav-bar::after{
        border: 0;
    }
    .leftBar {
        height: 46px;
        position: relative;
        z-index: 10;

        .backIcon {
            width: 32px;
            position: absolute;
            top: 0;
            left: -9px;
            z-index: 10;

            img {
                margin-top: 8px;
                width: 32px;
            }
        }

        .voiceIcon {
            width: 20px;
            height: 20px;
            position: absolute;
            top: 12px;
            right: 8px;
        }
    }
    .searchInput {
        width: 310px;
        height: 34PX;
        background: #F7F7F7;
        border-radius: 17PX;
        border: 0;
        padding-left: 32px;
        padding-right: 32px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #9A9A9A;
        text-align: left;
        letter-spacing: -0.04px;
        line-height: 34px;
        background-image: url(../../assets/images/mall/searchbar_icon_search_normal.png);
        background-repeat: no-repeat;
        -webkit-background-size: 14px 14px;
        background-size: 14px 14px;
        background-position: 10px 9px;
        position: relative;
        top: 6px;
        box-sizing: border-box;
        margin-left: 15px;
    }
    .leftBar {
        width: 100%;
        position: relative;
        padding-left: 15px;
        box-sizing: border-box;
        // position: fixed;
        // top: 0;
        display: flex;

        .searhInputBox {
            position: relative;
        }

        .searchInput {
            width: 250px;
            margin-right: 50px;
        }

        .voiceIcon {
            right: 58px;
        }

        .searchBtn {
            width: 82px;
            height: 34px;
            position: absolute;
            top: 6px;
            right: -33px;
            display: flex;

            align-items: center;

            img {
                width: 30px;
            }

            .collectBtn {
                margin-left: 5px;
            }

            .shopIcon {
                height: 30px;
                display: flex;
                /*margin-left: 5px;*/
                position: relative;

                /*.goodsNum {
                    width: 14px;
                    height: 14px;
                    text-align: center;
                    border-radius: 50%;
                    letter-spacing: -0.02px;
                    background: #FC0238;
                    position: absolute;
                    top: -3px;
                    right: -4px;
                    font-size: 9px;
                    color: #FFFFFF;
                    display:flex;
                    justify-content:center;
                    align-items:center
                }
                .goodsNumMax{
                    width: 18px;
                    border-radius: 10px;
                }*/
                .goodsNum {
                    width: 16px;
                    height: 16px;
                    text-align: center;
                    border-radius: 0.21333rem;
                    letter-spacing: -0.02px;
                    background: #FC0238;
                    position: absolute;
                    top: -3px;
                    right: -4px;
                    font-size: 0.26667rem;
                    color: #FFFFFF;
                    box-sizing: border-box;
                    display:flex;
                    justify-content:center;
                    align-items:center;
                    width: -webkit-max-content;
                    width: -moz-max-content;
                    width: max-content;
                    min-width: 0.42667rem;
                    height: 0.42667rem;
                }
                .goodsNumMax{
                    padding: 0 3px;
                    right: -12px;
                    min-width: 26px;
                }

            }
        }
    }
    .shopInfoBox{
        width: 100%;
        padding: 15px;
        box-sizing: border-box;
        .shopTitle{
            font-family: PingFangSC-Medium;
            font-size: 14px;
            color: #333333;
            letter-spacing: 0;
            font-weight: 600;
        }
        .shopImgBox{
            margin-top: 10px;
            margin-bottom: 15px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .shopImg{
                width: 70px;
                height: 70px;
                border-radius: 4px;
                overflow: hidden;
                img {
                    width: 70px;
                }
            }
            .shopInfo{
                .shopName{
                    width: 265px;
                    font-weight: 600;
                    font-family: PingFangSC-Medium;
                    font-size: 18px;
                    color: #333333;
                    letter-spacing: 0;
                    line-height: 22.5px;
                }
                .shopScore{
                    margin-top: 8px;
                    display: flex;
                    align-items: center;
                    .shopStars{
                        display: flex;
                        align-items: center;
                        img {
                            width: 14px;
                            height: 14px;
                            margin-right: 5px;
                        }
                    }
                    .shopStarNum{
                        font-family: PingFangSC-Medium;
                        font-size: 14px;
                        color: #FB5364;
                        letter-spacing: 0;
                    }
                    .noShopStarNum{
                        font-family: PingFangSC-Medium;
                        font-size: 14px;
                        color: #333333;
                        letter-spacing: 0;
                    }
                    .saleNum{
                        font-family: PingFangSC-Regular;
                        font-size: 14px;
                        color: #9A9A9A;
                        letter-spacing: 0;
                        margin-left: 15px;
                    }
                }
            }
        }
        .noticeTxt{
            margin-top: 5px;
            margin-bottom: 21px;
            width: 345px;
            font-family: PingFangSC-Regular;
            font-size: 14px;
            color: #333333;
            letter-spacing: 0;
            line-height: 20px;
        }
    }
    .manageClassifyList{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 16px;
        margin-bottom: 20px;
        .manageClassifyItem{
            width: 105px;
            height: 30px;
            margin-right: 15px;
            margin-bottom: 10px;
            background: #F7F7F7;
            border-radius: 4px;
            text-align: center;
            line-height: 30px;
            font-family: PingFangSC-Regular;
            font-size: 14px;
            color: #4A4A4A;
            letter-spacing: 0;
        }
        .manageClassifyItem:nth-child(3n){
            margin-right: 0px;
        }
    }
    .vanPopup{
        max-height: 327px;
        background: transparent;
        padding: 0 8px 8px;
        /* padding-bottom: 8px; */
        box-sizing: border-box;
        .phoneSelectBox{
            width: 345px;
            margin: 0 auto 10px;
            background: #F7F7F7;
            border-radius: 10px;
            .phoneSelect{
                height: 50px;
                font-family: PingFangSC-Regular;
                font-size: 14px;
                letter-spacing: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                border-bottom: 1px solid #E5E5E5;
                .phoneNum{
                    color: #4A90E2;
                }
            }
            .phoneSelect:last-child{
                border-bottom: 0px;
            }
        }
        .cancelPhoneBtn{
            width: 345px;
            height: 50px;
            line-height: 50px;
            text-align: center;
            border-radius: 10px;
            background: #fff;
            margin: 0 auto;
            font-family: PingFangSC-Regular;
            font-size: 16px;
            color: #3A3A3A;
            letter-spacing: 0.0016rem;
        }
    }
    .addressBox{
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        .shopAddress{
            width: 278px;
            margin-top: 5px;
            font-family: PingFangSC-Regular;
            font-size: 14px;
            color: #333333;
            letter-spacing: 0;
            line-height: 20px;
        }
        .phoneIcon{
            width: 55px;
            height: 23px;
            margin-left: 12px;
            img {
                width: 55px;
            }
        }
    }
    .synopsisTxt{
        margin-top: 5px;
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #333333;
        letter-spacing: 0;
    }
</style>
